import { useLazyQuery } from "@apollo/client";
import { useState } from "react";
import { Form } from "react-bootstrap";
import ReCAPTCHA from "react-google-recaptcha";
import { Link } from "react-router-dom";
import styled from "styled-components";
import FloatingInput from "../components/FloatingInput";
import MkkLogo from "../components/MkkLogo";
import { classLocale } from "../constants/lostark";
import { CHECK_CHARACTER } from "../constants/querys";

const SignupButton = styled.input`
	width: 100%;
	padding: 10px 0px;
	border-radius: 0;
	letter-spacing: -0.125rem;
`;

const StyledLink = styled(Link)`
	text-decoration: none;
`;
const SigninText = styled.p`
	font-size: 1.25rem;
	letter-spacing: -0.125rem;
`;

export default function Signup() {
	const [id, setId] = useState("");
	const [password, setPassword] = useState("");
	const [nickname, setNickname] = useState("");
	const [lostarkNickname, setLostarkNickname] = useState("");
	const [email, setEmail] = useState("");
	const [reCAPTCHAToken, setReCAPTCHAToken] = useState(null);
	const [lostarkNicknameCheck, setLostarkNicknameCheck] = useState(false);
	const onSubmit = (e) => {
		e.preventDefault();

		if (lostarkNicknameCheck === false) {
			alert("    ");
			return;
		}

		if (!reCAPTCHAToken) {
			alert("reCAPTCHA   ");
			return;
		}
	};

	const [checkCharacter] = useLazyQuery(CHECK_CHARACTER, {
		onCompleted: ({ character }) => {
			if (!character) {
				alert(`${lostarkNickname}    `);
				return;
			}
			var select = window.confirm(
				`: ${lostarkNickname}\n : ${
					character.itemLevel
				} : ${classLocale(
					character.class,
					"kr"
				)}\n  ?`
			);
			setLostarkNicknameCheck(select);
		},
	});

	return (
		<>
			<MkkLogo />
			<></>
			<Form
				className="form-sign"
				onSubmit={onSubmit}
				style={{ width: "350px" }}>
				<FloatingInput
					className="mb-3"
					name=""
					help="4-20    ."
					required
					autoFocus
					maxLength={20}
					pattern="[A-Za-z0-9]{4,20}"
					value={id}
					onChange={(e) => setId(e.target.value)}
				/>
				<FloatingInput
					type="password"
					className="mb-3"
					name=""
					help="8-20  ."
					maxLength={20}
					pattern=".{8,20}"
					autoComplete="new-password"
					required
					value={password}
					onChange={(e) => setPassword(e.target.value)}
				/>
				<FloatingInput
					className="mb-3"
					name=""
					help="1~10 , ,   ."
					maxLength={10}
					pattern="[A-Za-z0-9-]{1,10}"
					required
					value={nickname}
					onChange={(e) => setNickname(e.target.value)}
				/>
				<div className="input-group mb-3">
					<FloatingInput
						name=" "
						maxLength={12}
						pattern="[a-zA-Z-0-9]{1,12}"
						required
						value={lostarkNickname}
						onChange={(e) => {
							setLostarkNickname(e.target.value);
						}}
					/>
					<button
						className="btn btn-outline-secondary"
						type="button"
						onClick={() => {
							if (lostarkNickname.trim() === "") {
								return;
							}
							checkCharacter({
								variables: { name: lostarkNickname },
							});
						}}>
						
					</button>
				</div>

				<FloatingInput
					type="email"
					className="mb-3"
					name=""
					required
					value={email}
					onChange={(e) => setEmail(e.target.value)}
				/>
				<ReCAPTCHA
					className="mb-3"
					style={{ textAlign: "-webkit-center" }}
					sitekey="6LcZ-0AjAAAAAA1JDujfmnfOiqtjChfrVB68dxUf"
					onChange={(value) => setReCAPTCHAToken(value)}
				/>
				<SignupButton
					type="submit"
					className="btn btn-lg bgc-mkk"
					value=" "
				/>
			</Form>
			<hr style={{ width: "100%" }} />
			<div className="text-center">
				<StyledLink to="/signin/">
					<SigninText className="color-mkk"></SigninText>
				</StyledLink>
			</div>
		</>
	);
}
