export const classLocale = (enClass, locale) => {
	if (enClass === "") {
		return "???";
	}
	if (CLASS_LOCALE.hasOwnProperty(enClass) === false) {
		return enClass;
	}
	return CLASS_LOCALE[enClass][locale];
};
